</script>

<style>
.starbucks-head-img {
  width: 100%;
}
.new-sbux-rewards {
  background-color: #023529;
  position: relative;
  width: 100%;
  padding: 48px 0 60px;
}
.member-star-acquire {
  background-color: #023529;
  position: relative;
  width: 100%;
  /* padding: 48px 0 60px; */
}
.new-sbux-rewards::before {
  content: ' ';
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 0;
  border-width: 14px;
  border-style: solid;
  border-color: #14767c transparent transparent transparent;
}

.page-account-starbucks-rewards .new-sbux-rewards .title {
  margin-bottom: 24px;
}
.page-account-starbucks-rewards section .title {
  width: 640px;
  height: auto;
  margin: auto;
  color: #ffffff;
  text-align: center;
}
.item {
  padding: 0 12px;
  box-sizing: border-box;
}

.page-account-starbucks-rewards .tips {
  left: 0;
  width: 100%;
  text-align: center;
  position: absolute;
  bottom: 16px;
  font-size: 12px;
  padding: 0 16px;
  line-height: 140%;
  box-sizing: border-box;
}
/* 3 */
.image {
  margin: 0 auto;
  display: inherit;
  width: 140px;
}
/* 4 */

.star-list {
  list-style: none;
  margin: 0;
  padding: 0 20px;
  /* border-bottom: 1px solid #e8ebea; */
}
.star-list-active {
  border-bottom: 4px solid #006441;
}
.account_app .redeem-right-for-website .content-wrapper {
  position: relative;
  display: flex;
  background-color: #ebf3f5;
  padding: 0 80px 0 86px;
}

.content-left {
  flex: 1;
  margin-top: 72px;
  min-width: 0;
  padding-right: 70px;
}
.content-image {
  flex: 0 0 320px;
  text-align: center;
}
/* 5 */
.page-account-starbucks-rewards .member-package {
  width: 100%;
  padding: 48px 0 48px 0;
}
/* 8 */
.page-account-starbucks-rewards .member-faq {
  background-color: #fff;
  padding: 30px 0;
}
.page-account-starbucks-rewards .member-faq .card {
  box-shadow: none;
  padding-left: 20px;
  padding-right: 20px;
}
.header img {
  display: inline-block;
  width: 54px;
  height: auto;
  vertical-align: middle;
}
/* footer */
.libra-msr-footer {
  background: url(../../assets/image/account/footer-new.jpeg) no-repeat 0 -18px #013529;
  background-size: cover;
  margin-top: 0;
}
</style>


