<template>
  <section class="program-club">
    <!-- bootstrapflexbox -->
    <div class="club-box row">
      <!-- colcol  col/100 col50%-->
      <div class="club-left col">
        <h2 class="h2 mb-4"></h2>
        <p class="text-secondary mb-4">
          <a
            class="text-success"
            href="https://www.starbucks.com.cn/account/starbucks-rewards"
            target="_blank"
            > </a
          >
        </p>
        <!--  logo -->
        <account-component></account-component>
      </div>
      <div class="club-right col">
        <img src="@/assets/image/logo-msr-new.svg" />
      </div>
      <!-- bootstrap -->
      <div class="clearfix"></div>
    </div>
  </section>
</template>

<script>
import accountComponent from './accountComponent'
export default {
  components: {
    accountComponent,
  },
}
</script>

<style>
h1,
h2,
h3,
h4,
h5,
h6 {
  margin-bottom: 18px;
}
.program-club {
  overflow: hidden;
  z-index: 100;
}
.club-box {
  display: flex;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
  margin-left: auto;
  margin-right: auto;
  max-width: 768px;
  width: 100%;
}
.club-left,
.club-right {
  padding-top: 36px;
  padding-bottom: 36px;
}
</style>


