import Vue from 'vue'
import Router from 'vue-router'
import index from '@/components/index'
import indexleft from '@/components/indexleft'//PC
import indexright from '@/components/indexright'//PC


// 
import stores from '../views/stores/stores.vue'
// 
import menu from '../views/menu/menu.vue'// 
import menuIndex from '../views/menu/menuIndex.vue'//menu
import beverages from '@/views/menu/beverages.vue'
import food from '@/views/menu/food.vue'
import coffee from '@/views/menu/coffee.vue'
import merchandise from '@/views/menu/merchandise.vue'

// 
import account from '@/views/account/account.vue'// 
import accountIndex from '@/views/account/accountIndex.vue'//account
import register from '@/views/account/register.vue'// 
import starbucksrewards from '@/views/account/starbucks-rewards.vue' //


//~
Vue.use(Router)

export default new Router({
  //  ~
  mode: 'history',
  // linkActiveClass,           css  .xxx{} 
  // linkExactActiveClass,  .linkActiveClass{}, .linkExactActiveClass{} ; 
  //  https://router.vuejs.org/zh/api/#linkactiveclass 
  // 
  routes: [
    {
      path: '/',
      name: 'index',
      //    
      components: {
        default: index,
        indexleft: indexleft,
        indexright: indexright
      },
      //  /   DOM
      children: [
        {
          path: '/stores',
          name: 'stores',
          component: stores
        },
        {
          path: '/menu',
          name: 'menu',
          //  /  
          component: menu,
          children: [
            {
              //   v-show @/components/indexleft.vue 
              path: '/menu',
              name: 'menuIndex',
              component: menuIndex
            },
            { //  menu  /menu//  /menu/ 
              //  
              path: 'beverages',
              name: 'beverages',
              component: beverages,
            }, {
              path: 'food',
              name: 'food',
              component: food,
            }, {
              path: 'coffee',
              name: 'coffee',
              component: coffee,
            }, {
              path: 'merchandise',
              name: 'merchandise',
              component: merchandise,
            },
          ]
        },
        {
          path: '/account',
          name: 'account',
          component: account,
          children: [
            {
              path: '/account',
              name: 'accountIndex',
              component: accountIndex,
            },
            {
              path: 'register',
              name: 'register',
              component: register,
            },
            {
              path: 'starbucks-rewards',
              name: 'starbucksrewards',
              component: starbucksrewards,
            }
          ]
        }
      ]
    },
    {
      // http://localhost:8080home
      //  alias 
      // /aURL /b/b
      // /a/b/b URL /b/a/a 
      path: '',
      redirect: '/'
    },
  ]
})



