          { title: '', path: 'beverages' },
          { title: '', path: 'food' },
          { title: '', path: 'coffee' },
          { title: '', path: 'merchandise' },
        ],
      },
      second_meun: {
        beverages: [
          { path: '/menu/beverages', id: '1', title: '', SubTypes: 'all' },
          {
            path: '/menu/beverages',
            id: '2',
            title: '',
            SubTypes: 'mixology',
          },
          {
            path: '/menu/beverages',
            id: '3',
