            SubTypes: 'frappu',
          },
          {
            path: '/menu/beverages',
            id: '5',
            title: '',
            SubTypes: 'chawana',
          },
          {
            path: '/menu/beverages',
            id: '6',
            title: '',
            SubTypes: 'chocolate',
          },
          {
            path: '/menu/beverages',
            id: '7',
            title: '',
