        coffee: [
          { path: '/menu/coffee', id: '1', title: '', SubTypes: 'all' },
          {
            path: '/menu/coffee',
            id: '2',
            title: '',
            SubTypes: 'baking-M',
          },
          {
            path: '/menu/coffee',
            id: '3',
            title: 'VIA ',
            SubTypes: 'via-black',
          },
          {
            path: '/menu/coffee',
            id: '4',
            title: '',
            SubTypes: 'selection',
          },
          {
            path: '/menu/coffee',
            id: '5',
            title: 'Origami',
            SubTypes: 'origami',
          },
        ],
        merchandise: [
          {
            path: '/menu/merchandise',
            id: '1',
            title: '',
            SubTypes: 'all',
          },
          {
            path: '/menu/merchandise',
            id: '2',
            title: '',
            SubTypes: 'ordinary',
          },
          {
            path: '/menu/merchandise',
