  created() {
    // 
    this.fetchData()
  },
  watch: {
    // 
    '$route.path': 'fetchData',
  },
  methods: {
    fetchData() {
      //  menuClass
      this.menuClass = 0
      // axios
      this.loading = true
      // 
      this.$store.dispatch('filterMenu', 'all')
      console.log('', this.$route.path)
    },
    /*  routeactive ;
     router-linkclass 
     
     1. menuClass = 0v-for0  ;
     2.index=1index :index(1)menuClass;
     3. html (:class="{'xxx':xxx?'xxx'}")  index == 1 == menuClass ture OKindex != menuClass 
     4.   fetchData menuClass  0
      ~
     */
    addMenuClass(index) {
      this.menuClass = index
    },
    // 
    menuShow() {
      this.$store.dispatch('menuShow')
    },
    // 
    filterMenu(index) {
      this.$store.dispatch('filterMenu', index)
    },
  },
}
</script>

